@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: normal;
  src: url("/fonts/oswaldregular.woff2") format("woff2"), url("/fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/oswaldmedium.woff2") format("woff2"), url("/fonts/oswaldmedium.woff2") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}
.page__body {
  min-height: 100%;
  background-color: #ffffff;
}
.header {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  color: #000000;
}
.site-menu {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  list-style: none;
  text-align: center;
}
.site-menu__link {
  color: #000000;
  text-decoration: none;
}
.promo {
  padding: 20px;
  color: #ffffff;
  background-color: #68b738;
  background-image: url("../img/backgrounds/background-promo.jpg");
  text-align: center;
}
.promo__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}
.promo__description {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 14px;
  text-transform: uppercase;
}
.button {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 18px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
.programs__item {
  padding: 20px;
  background-color: #f2f2f2;
}
.programs__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
.programs__description {
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
}
.programs__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
}
.usage {
  padding: 20px;
}
.usage__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
.usage__list {
  list-style: none;
  color: #444444;
}
.example {
  padding: 20px;
  background-color: #eaeaea;
}
.example__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
.example__description {
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
}
